import { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import './Carousel.css'
import './Carousel-default-theme.css'

/**
 *
 * @param {Object} props
 * @param {string} props.className - A css class that can be added to the carousel.
 * @param {boolean} props.pictures - An array of links to images.
 * @returns
 */
function Carousel({ className, pictures }) {
  const [current, setCurrent] = useState(0)
  const [currents, setCurrents] = useState([0])
  const { length } = pictures

  useEffect(() => {
    const updateCurrents = () => {
      if (length > 2) {
        switch (current) {
          case 0:
            setCurrents([length - 1, current, current + 1])
            break
          case length - 1:
            setCurrents([current - 1, current, 0])
            break
          default:
            setCurrents([current - 1, current, current + 1])
            break
        }
      } else if (length === 2) {
        switch (current) {
          case 1:
            setCurrents([current, 0])
            break
          default:
            setCurrents([current, 1])
            break
        }
      } else setCurrents([current])
    }
    updateCurrents()
  }, [current, length])

  const prevSlide = () => {
    setCurrent(current === 0 ? length - 1 : current - 1)
  }

  const nextSlide = () => {
    setCurrent(current === length - 1 ? 0 : current + 1)
  }

  const pictureElements = currents.map((value, index) => (
    <div
      className={
        index === 1 ? 'carousel__item carousel__item-current' : 'carousel__item'
      }
      key={`carousel-${pictures[value]}`}
    >
      <img className="carousel__img" src={pictures[value]} alt="" />
    </div>
  ))

  return (
    <div className={`carousel ${className}`}>
      {pictureElements}
      <button
        type="button"
        onClick={prevSlide}
        className="carousel__btn carousel__btn-prev"
      >
        <span className="carousel__btn__text">previous picture</span>
      </button>
      <button
        type="button"
        onClick={nextSlide}
        className="carousel__btn carousel__btn-next"
      >
        <span className="carousel__btn__text">next picture</span>
      </button>
    </div>
  )
}

export default Carousel

Carousel.defaultProps = {
  className: '',
}

Carousel.propTypes = {
  className: PropTypes.string,
  pictures: PropTypes.arrayOf(PropTypes.string).isRequired,
}
